<template>
  <Window
    title="Cущность"
    :page-mode="true"
    class="character-view container"
    :show-button-close="true"
    @close-click="$router.push({ name: 'Home' })"
    :style="!isCharacterDataLoaded && 'cursor: progress'"
  >
    <template v-if="isCharacterDataLoaded">
      <div class="row">
        <div class="col">
          <Identity />
          <Holdables :entity="characterModel" />
        </div>
        <div class="col-auto">
          <SkinViewer
            skin-link="https://www.minecraftskins.com/uploads/skins/2022/08/16/skeleton-fella-chill-20732795.png?v527"
          >
            {{ $store.state.character.oath }}
          </SkinViewer>
        </div>
        <div class="col">
          <BodyParts :entity="characterModel" />
          <Stats :entity="characterModel" />
        </div>
      </div>
      <div class="row mt-3">
        <div class="col-3">
          <Skills :skillsModel="$store.state.character.skills" />
        </div>
        <div class="col-2">
          <Knowledge :knowledgeModel="$store.state.character.knowledge" />
          <!-- TODO: add psychosis box here  -->
        </div>
        <div class="col">
          <Abilities
            :abilitiesModel="this.$store.state.character.abilities"
            :generalBuffsModel="this.$store.state.character.general_buffs"
          />
        </div>
        <div class="col-3"><Attributes /></div>
      </div>
      <div class="row mt-3">
        <div class="col"><CharacterNarrations /></div>
        <div class="col"><InfosphereNarrations /></div>
      </div>
    </template>
  </Window>
</template>

<script>
import Window from "@/components/Window";
import SkinViewer from "@/components/SkinViewer";
import Identity from "@/components/CharacterSections/Identity";
import Holdables from "@/components/CharacterSections/Holdables";
import BodyParts from "@/components/CharacterSections/BodyParts";
import Skills from "@/components/CharacterSections/Skills";
import Stats from "@/components/CharacterSections/Stats";
import Attributes from "@/components/CharacterSections/Attributes";
import Abilities from "@/components/CharacterSections/Abilities";
import CharacterNarrations from "@/components/CharacterSections/CharacterNarrations";
import InfosphereNarrations from "@/components/CharacterSections/InfosphereNarrations";
import { createRequest } from "@/api/requestBuilder";
import requestConfigs from "@/api/requestConfigs";
import Knowledge from "@/components/CharacterSections/Knowledge.vue";

export default {
  name: "Character",
  data() {
    return {
      isCharacterDataLoaded: false,
    };
  },
  components: {
    Knowledge,
    InfosphereNarrations,
    CharacterNarrations,
    Abilities,
    Attributes,
    Stats,
    Skills,
    BodyParts,
    Holdables,
    Identity,
    SkinViewer,
    Window,
  },
  computed: {
    characterModel() {
      return this.$store.state.character;
    },
  },
  beforeMount() {
    createRequest(requestConfigs.GETCharacterInfo, {
      queryPayload: {
        characterName: this.$store.state.selectedCharacterName,
      },
    }).then((response) => {
      this.$store.commit("dumbMutation", {
        field: "character",
        value: response.data,
      });
      this.isCharacterDataLoaded = true;
    });
  },
};
</script>

<style scoped lang="scss">
.character-view {
  height: 100%;
  padding: 2px;

  .col {
    display: flex;
    flex-direction: column;
  }
  .row {
    margin-right: 0;
  }
}
</style>
