<template>
  <div class="narrations bordered clear-bg">
    <ul class="tree-view">
      <li><b>Внешность: </b> {{ character.appearance || "НЕТ ДАННЫХ" }}</li>
      <li>
        <b>Перки:</b>
        <template v-if="percsList.length === 0"> НЕТ ДАННЫХ </template>
        <ul v-else>
          <li v-for="(item, index) in percsList" :key="index">
            <b>{{ item[0] }}:</b> {{ item[1] }}
          </li>
        </ul>
      </li>
      <li>
        <b>Мысли:</b>
        <template v-if="character.thoughts.length === 0"> НЕТ ДАННЫХ </template>
        <ul class="thoughts" v-else>
          <li v-for="(item, index) in character.thoughts" :key="index">
            {{ item }}
          </li>
        </ul>
      </li>
    </ul>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "CharacterNarrations",
  computed: {
    percsList() {
      return Object.entries(this.character.narrative_perks);
    },
    ...mapState(["character"]),
  },
};
</script>

<style lang="scss">
.narrations {
  text-align: left;
  .tree-view {
    min-height: 200px;
  }
  .thoughts {
    font-style: italic;
  }
}
</style>
