<template>
  <div class="infosphere-narrations">
    <TerminalBlock>
      <div class="narration-item">
        <div class="narration-item__header">Инфосфера считает, что...</div>
        <div class="narration-item__text">
          {{ protectedInfosphereOpinion }}
        </div>
      </div>
      <div class="narration-item" v-if="character.gossips.length > 0">
        <div class="narration-item__header">Говорят, что...</div>
        <ul class="narration-item__text">
          <li v-for="(item, index) in character.gossips" :key="index">
            {{ item.gossip }}
            (<i class="ra" :class="gossipTypes[item.gossip_type].icon" />)
          </li>
        </ul>
      </div>
    </TerminalBlock>
  </div>
</template>

<script>
import TerminalBlock from "@/components/TerminalBlock";
import gossipTypes from "@/enums/gossipTypes";
import dummyInfosphereOpinions from "@/enums/dummyInfosphereOpinions";
import { mapState } from "vuex";

export default {
  name: "InfosphereNarrations",
  data() {
    return {
      gossipTypes,
    };
  },
  components: { TerminalBlock },
  computed: {
    protectedInfosphereOpinion() {
      if (this.character.infosphere_opinion)
        return this.character.infosphere_opinion;

      //return random element from dummyInfosphereOpinions array
      return dummyInfosphereOpinions[
        Math.floor(Math.random() * dummyInfosphereOpinions.length)
      ];
    },
    ...mapState(["character"]),
  },
};
</script>

<style lang="scss">
.infosphere-narrations {
  color: white;
  font-family: "Press Start 2P", cursive;
  text-align: left;

  .terminal-block {
    min-height: 200px;
  }
  .narration-item {
    margin-bottom: 15px;

    &:not(:last-child):after {
      content: "***";
      text-align: center;
      display: block;
      margin-top: 15px;
    }

    &__header {
      margin-bottom: 10px;
      font-weight: bold;
    }
    &__text {
      font-size: 12px;
    }

    ul {
      list-style-type: disclosure-closed;
      padding-left: 15px;
      line-height: 1.4;
      margin: 0;

      i {
        font-size: 18px;
      }
    }
  }
}
</style>
