<template>
  <div class="skills bordered clear-bg p-2">
    <header>
      <i class="ra ra-wrench" /> Навыки <i class="ra ra-wrench" />
    </header>
    <div class="skill" v-for="(item, index) in skillsList" :key="index">
      <div>{{ item.skillPoints }}</div>
      <div>{{ item.name }}</div>
    </div>
  </div>
</template>

<script>
import skillsEnum from "@/enums/skillsEnum";

export default {
  name: "Skills",
  props: ["skillsModel"],
  computed: {
    skillsList() {
      return Object.keys(skillsEnum).map((item) => {
        return {
          skillPoints: this.skillsModel[item],
          name: skillsEnum[item],
        };
      });
    },
  },
};
</script>

<style lang="scss">
.skills {
  header {
    font-weight: bold;
    text-align: center;
    border-bottom: 1px dotted #1f5193;
    padding-bottom: 5px;
    color: #6b7742;
  }
  .skill {
    display: flex;
    &:not(:last-child) {
      border-bottom: 1px dotted #1f5193;
    }

    > * {
      padding: 5px;
      &:not(:last-child) {
        border-right: 1px dotted #1f5193;
      }
      &:first-child {
        color: #6b7742;
        font-family: "Press Start 2P", cursive;
        font-size: 12px;
        width: 15%;
        min-width: 15%;
      }
    }
  }
}
</style>
