<template>
  <div class="knowledge bordered clear-bg p-2">
    <header>
      <i class="ra ra-light-bulb" /> Знания <i class="ra ra-light-bulb" />
    </header>
    <div
      class="knowledge__row"
      v-for="(item, index) in knowledgeList"
      :key="index"
    >
      <div>{{ item.points }}%</div>
      <div>{{ item.name }}</div>
    </div>
  </div>
</template>

<script>
import knowledgesEnum from "@/enums/knowledgesEnum";
export default {
  name: "Knowledge",
  props: ["knowledgeModel"],
  computed: {
    knowledgeList() {
      return Object.entries(knowledgesEnum).map((item) => {
        return {
          points: this.knowledgeModel[item[0]],
          name: item[1],
        };
      });
    },
  },
};
</script>

<style lang="scss">
.knowledge {
  header {
    font-weight: bold;
    text-align: center;
    border-bottom: 1px dotted #1f5193;
    padding-bottom: 5px;
    color: #4e4277;
  }
  .knowledge__row {
    display: flex;
    &:not(:last-child) {
      border-bottom: 1px dotted #1f5193;
    }

    > * {
      padding: 5px;
      &:not(:last-child) {
        border-right: 1px dotted #1f5193;
      }
      &:first-child {
        color: #4e4277;
        font-family: "Press Start 2P", cursive;
        font-size: 12px;
        width: 20%;
        min-width: 20%;
      }
    }
  }
}
</style>
