<template>
  <Window
      title="Редактор реальности"
      :page-mode="true"
      class="admin-view"
      :show-button-close="true"
      @close-click="$router.push({ name: 'Home' })"
  >
    <CustomForm
        :field-list="addTestCharecterList"
        :title="'Создание персонажа в спайсе'"
        @finish="(list) => {
      sendFormRequest(list, 'POSTCreateTestCharacter')
    }"/>
    Бля, только не говнокодь сильно....
    <CustomForm
        :field-list="addAbilityList"
        :title="'Добавление абилки'"
        @finish="(list) => {
      sendFormRequest(list, 'POSTAddAbility')
    }"/>
    <CustomForm
        :field-list="removeAbilityList"
        :title="'Удаление абилки'"
        @finish="(list) => {
      sendFormRequest(list, 'POSTRemoveAbility')
    }"/>
    <CustomForm
        :field-list="editAttributeList"
        :title="'Изменение атрибута'"
        @finish="(list) => {
      sendFormRequest(list, 'POSTModifyAttribute')
    }"/>
    <CustomForm
        :field-list="modifySkillList"
        :title="'Изменение скилла'"
        @finish="(list) => {
      sendFormRequest(list, 'POSTModifySkill')
    }"/>
    <CustomForm
        :field-list="modifyStatList"
        :title="'Изменение статы'"
        @finish="(list) => {
      sendFormRequest(list, 'POSTModifyStat')
    }"/>
    <CustomForm
        :field-list="addBodypartList"
        :title="'Добавление части тела'"
        @finish="(list) => {
      sendFormRequest(list, 'POSTAddBodypart')
    }"/>
  </Window>
</template>

<script>
import Window from "@/components/Window.vue";
import CustomForm from "@/components/AdminPanel/CustomForm.vue";
import requestAdminConfig from "@/api/requestAdminConfig";
import {createRequest} from "@/api/requestBuilder";
import {attributeList, modList, skillList, statList} from "@/enums/adminFormConfig";

export default {
  name: "AdminPanel",
  components: {CustomForm, Window},
  data() {
    return {
      addTestCharecterList: [
        {
          label: "Имя персонажа",
          value: '',
          name: 'name',
          type: 'input'
        },
        {
          label: "Пароль",
          value: '',
          name: 'password',
          type: 'input'
        },
        {
          label: "Видимое имя",
          value: '',
          name: 'display_name',
          type: 'input'
        },
      ],
      addAbilityList: [
        {
          label: "Имя персонажа",
          value: '',
          name: 'entityName',
          type: 'input'
        },
        {
          label: "Название абилки",
          value: '',
          name: 'abilityName',
          type: 'input'
        },
      ],
      removeAbilityList: [
        {
          label: "Имя персонажа",
          value: '',
          name: 'entityName',
          type: 'input'
        },
        {
          label: "Название абилки",
          value: '',
          name: 'abilityName',
          type: 'input'
        },
      ],
      editAttributeList: [
        {
          label: "Имя персонажа",
          value: '',
          name: 'entityName',
          type: 'input'
        },
        {
          label: "Название атрибута",
          value: '',
          optionList: attributeList,
          name: 'attributeName',
          type: 'dropdown'
        },
        {
          label: "Модификатор",
          value: '',
          optionList: modList,
          name: 'mod',
          type: 'dropdown'
        },
      ],
      modifySkillList: [
        {
          label: "Имя персонажа",
          value: '',
          name: 'entityName',
          type: 'input'
        },
        {
          label: "Название скилла",
          value: '',
          optionList: skillList,
          name: 'skillName',
          type: 'dropdown'
        },
        {
          label: "Модификатор",
          value: '',
          optionList: modList,
          name: 'mod',
          type: 'dropdown'
        },
      ],
      modifyStatList: [
        {
          label: "Имя персонажа",
          value: '',
          name: 'entityName',
          type: 'input'
        },
        {
          label: "Название статы",
          value: '',
          optionList: statList,
          name: 'statName',
          type: 'dropdown'
        },
        {
          label: "Модификатор",
          value: '',
          optionList: modList,
          name: 'mod',
          type: 'dropdown'
        },
      ],
      addBodypartList: [
        {
          label: "Имя персонажа",
          value: '',
          name: 'entityName',
          type: 'input'
        },
        {
          label: "Часть тела",
          value: '',
          name: 'bodypart',
          type: 'input'
        },
        {
          label: "Ключ",
          value: '',
          name: 'key',
          type: 'input'
        },
        {
          label: "Текущее хп",
          value: '',
          name: 'currHp',
          type: 'input'
        },
      ],
    }
  },
  methods: {
    sendFormRequest(list, config) {
      const formData = list.reduce((acc, {name, value}) => {
        acc[name] = value;
        return acc;
      }, {})
      createRequest(requestAdminConfig[config], {
        jsonPayload: formData,
      })
          .then((result) => {
            this.$store.commit("pushToTray", result.data.message);
          })
          .catch((err) => {
            this.$store.commit("pushToTray", err.response.data.message);
          })

    }
  }
};
</script>

<style lang="scss">
.admin-view {
  width: 100%;
  max-width: 1000px;
  height: 100%;
}
</style>
