<template>
  <section>
    <h4 class="custom_form__h">{{ title }}</h4>
    <o-field
        v-for="(item, index) in fieldList.filter((el) => el.type === 'input')"
        :key="index"
        :label="item.label"
        class="padding"
    >
      <o-input v-model="item.value"></o-input>
    </o-field>
    <o-field
        v-for="(item, index) in fieldList.filter((el) => el.type === 'dropdown')"
        :key="index"
        :label="item.label"
        class="padding"
    >
      <o-select v-model="item.value">
        <option
            v-for="(el, index) in item.optionList"
            :key="index"
            :value="el.value"
        >
          {{ el.label }}
        </option>
      </o-select>
    </o-field>
    <o-button @click="$emit('finish', fieldList)">Изменить</o-button>
    <hr />
  </section>
</template>

<script>
export default {
  name: "DropdownForm",
  props: {
    fieldList: {
      type: Array,
      required: true
    },
    title: {
      type: String,
    }
  },
  data() {
    return {
      attributeInfo: {
        entityName: '',
        attributeName: 'STRENGTH',
        mod: '+1',
      }
    }
  }
}
</script>

<style lang="scss">
.custom_form__h {
  margin: 0;
  padding: 5px;
}
.padding {
  padding: 5px;

  label {
    padding-bottom: 5px;
  }
}
</style>