<template>
  <div class="holdables bordered clear-bg">
    <ul class="holdables__container tree-view tree-view_with-links">
      <li>
        <b>Инвентарь</b>
        <ul>
          <li><i class="ra ra-hand" /> Основной холдабл</li>
          <ul>
            <li>Мультитул</li>
          </ul>
        </ul>
        <ul>
          <li>
            <i
              class="ra ra-hand"
              style="transform: rotateY(180deg); display: inline-block"
            />
            Вторичный холдабл
          </li>
          <ul>
            <li>Ведро пеницилина</li>
          </ul>
        </ul>
        <ul>
          <li><i class="ra ra-zebra-shield" /> Энергощит</li>
          <ul>
            <li>Мощнощит атомного деления</li>
          </ul>
        </ul>
        <ul>
          <li><i class="ra ra-ankh" /> Тринкеты</li>
          <ul>
            <li
              v-for="(item, index) in entity.trinkets"
              @click="expandTrinket(item)"
              :key="'trinket' + index"
            >
              {{ item.trinket.display_name }}
            </li>
          </ul>
        </ul>
        <ul>
          <li><i class="ra ra-vest" /> Одежда</li>
          <ul>
            <li>Модный пиджак</li>
            <li>Офигенный галстук</li>
          </ul>
        </ul>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "Holdables",
  props: {
    entity: {
      type: Object,
    },
  },
  methods: {
    expandTrinket(trinket) {
      this.$store.commit("addPopup", {
        component: "ItemPopup",
        title: trinket.trinket.display_name,
        params: {
          itemType: "trinket",
          id: trinket.item,
        },
      });
    },
  },
};
</script>

<style scoped lang="scss">
.holdables {
  min-height: 300px;
  min-width: 250px;
  margin-top: 10px;
  padding: 5px;
  flex: 2;
  position: relative;

  &__container {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    overflow-y: scroll;
    padding: 10px;
    text-align: left;
  }
}
</style>
