<template>
  <div class="clear-bg bordered body-parts">
    <ul class="tree-view body-parts__container">
      <li>
        <b>Тело</b>
        <ul>
          <li
            v-for="(part, partIndex) in bodyPartsUnwrapped"
            :key="'body-part' + partIndex"
          >
            <span class="link-hover-behavior">
              <i class="ra ra-hand-emblem" />
              {{ part.bodypart.display_name }}
            </span>
            <ul v-if="part.armor_element.item?._id">
              <li>
                <span
                  class="link-hover-behavior"
                  @click="expandWearable(part.armor_element)"
                >
                  <i class="ra ra-helmet" />
                  {{ part.armor_element.item.display_name }}
                </span>
              </li>
            </ul>
          </li>
        </ul>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "BodyParts",
  props: {
    entity: {
      type: Object,
    },
  },
  computed: {
    bodyPartsUnwrapped() {
      return this.entity.parts.map((part) => Object.values(part)[0]);
    },
  },
  methods: {
    expandWearable(wearableContainer) {
      this.$store.commit("addPopup", {
        component: "ItemPopup",
        title: wearableContainer.item.display_name,
        params: {
          itemType: "wearable",
          id: wearableContainer.item._id,
          container: wearableContainer,
        },
      });
    },
  },
};
</script>

<style lang="scss">
.body-parts {
  min-height: 300px;
  min-width: 250px;
  flex: 2;
  position: relative;

  &__container {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    overflow-y: scroll;
    padding: 10px;
    text-align: left;
  }
}
</style>
